  .mfp-arrow .mfp-a {\r\n        margin-top: 15px;\r\n        top: -1px !important;\r\n    }\r\n\r\nimg.mfp-img {\r\n    padding: 40px 0px 0;\r\n}\r\n\r\n.mfp-fade.mfp-wrap .mfp-content {\r\n    opacity: 0;\r\n    transform: scale(0.95);\r\n    -webkit-transition: all 0.4s ease-out;\r\n    -moz-transition: all 0.4s ease-out;\r\n    transition: all 0.4s ease-out;\r\n}\r\n\r\n.mfp-fade.mfp-wrap.mfp-ready .mfp-content {\r\n    transform: scale(1);\r\n    opacity: 1;\r\n    -webkit-transition: all 0.4s ease-out;\r\n    -moz-transition: all 0.4s ease-out;\r\n    transition: all 0.4s ease-out;\r\n}\r\n\r\n.mfp-fade.mfp-wrap.mfp-removing .mfp-content {\r\n    opacity: 0;\r\n    transform: scale(0.95);\r\n    -webkit-transition: all 0.4s ease-out;\r\n    -moz-transition: all 0.4s ease-out;\r\n    transition: all 0.4s ease-out;\r\n}\r\n\r\n/* \r\n\r\n====== \"Hinge\" close effect ======\r\n\r\n*/\r\n@keyframes hinge {\r\n    0% {\r\n        transform: rotate(0);\r\n        transform-origin: top left;\r\n        animation-timing-function: ease-in-out;\r\n    }\r\n\r\n    20%, 60% {\r\n        transform: rotate(80deg);\r\n        transform-origin: top left;\r\n        animation-timing-function: ease-in-out;\r\n    }\r\n\r\n    40% {\r\n        transform: rotate(60deg);\r\n        transform-origin: top left;\r\n        animation-timing-function: ease-in-out;\r\n    }\r\n\r\n    80% {\r\n        transform: rotate(60deg) translateY(0);\r\n        opacity: 1;\r\n        transform-origin: top left;\r\n        animation-timing-function: ease-in-out;\r\n    }\r\n\r\n    100% {\r\n        transform: translateY(700px);\r\n        opacity: 0;\r\n    }\r\n}\r\n\r\n.hinge {\r\n    animation-duration: 1s;\r\n    animation-name: hinge;\r\n}\r\n\r\n.mfp-with-fade .mfp-content,\r\n.mfp-with-fade.mfp-bg {\r\n    opacity: 0;\r\n    transition: opacity .5s ease-out;\r\n}\r\n\r\n.mfp-with-fade.mfp-ready .mfp-content {\r\n    opacity: 1;\r\n}\r\n\r\n.mfp-with-fade.mfp-ready.mfp-bg {\r\n    opacity: 0.8;\r\n}\r\n\r\n.mfp-with-fade.mfp-removing.mfp-bg {\r\n    opacity: 0;\r\n}\r\n\r\n/* Pagenation */\r\n.portfolio-pagination {\r\n    text-align: center;\r\n    margin-top: 30px;\r\n}\r\n\r\n.pagination {\r\n    margin: 0px;\r\n}\r\n\r\n    .pagination li {\r\n        display: inline-block;\r\n        margin-bottom: 10px;\r\n        height: auto;\r\n    }\r\n\r\n        .pagination li a {\r\n            width: 42px;\r\n            height: 42px;\r\n            line-height: 42px;\r\n            padding: 0px;\r\n            margin: 0 8px;\r\n            border-radius: 3px;\r\n            border: 1px solid #fff;\r\n            background-color: #ffffff;\r\n        }\r\n\r\n            .pagination li a i {\r\n                line-height: 42px;\r\n            }\r\n\r\n.white-bg .pagination li a {\r\n    background-color: #F4F4F4;\r\n}\r\n\r\n.pagination li:first-child a,\r\n.pagination li:last-child a {\r\n    border: 1px solid #0CA579;\r\n}\r\n\r\n.pagination li a:hover,\r\n.pagination .active a,\r\n.pagination a:active,\r\n.pagination .active a:hover,\r\n.pagination .active a:focus,\r\n.pagination a:focus {\r\n    background-color: transparent;\r\n    border-color: #0CA579;\r\n    color: #0CA579;\r\n}\r\n\r\n.portfolio-pagination .pagination li.active {\r\n    background-color: none;\r\n}\r\n\r\n    .portfolio-pagination .pagination li.active a {\r\n        background: #0CA579;\r\n    }\r\n\r\n        .portfolio-pagination .pagination li.active a:hover {\r\n            color: #ffffff;\r\n        }\r\n\r\n/* Portfolios version 2 */\r\n.single-hr-p-content {\r\n    box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.05);\r\n    webkit-transition: all 0.4s ease-in-out;\r\n    -moz-transition: all 0.4s ease-in-out;\r\n    -o-transition: all 0.4s ease-in-out;\r\n    transition: all 0.4s ease-in-out;\r\n    margin-bottom: 30px;\r\n}\r\n\r\n    .single-hr-p-content:hover {\r\n        box-shadow: 0 8px 48px 0 rgba(0, 0, 0, 0.15);\r\n    }\r\n\r\n    .single-hr-p-content ul {\r\n  